* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: 'Poppins', sans-serif;
}

nav {
    height: 70px;
    display: flex;
    padding: 20px 36px;
    box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}

.bold {
    font-weight: bold;
}

.gray {
    color: #918E9B;
}

.nav--logo {
    max-width: 100px;
}

section {
    padding: 20px;
}

.hero {
    display: flex;
    flex-direction: column;
}

.hero--photo {
    max-width: 400px;
    align-self: center;
}

.hero--header {
    margin-bottom: 16px;
}

.hero--text {
    margin-top: 0;
}

.cards--container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.card {
    position: relative;
    margin: 10px 0;
    width: 175px;
    height: 330px;
    font-size: 12px;
}

.card--badge {
    padding: 4px;
    background-color: white;
    position: absolute;
    top: 10px;
    left: 10px;
    font-weight: bold;
    border-radius: 3px;
}

@media (min-width: 600px) {
    .cards--container {
        flex-direction: row;
        justify-content: space-evenly;
    }
}

.card--image {
    width: 100%;
    border-radius: 9px;
}

.card--stats {
    display: flex;
    align-items: center;
}

.card--star {
    height: 14px;
}

footer {
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 50px;
    font-size: 13px;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}